import React from 'react';
import styled from 'styled-components';

const StripeConnect = styled.a`
    display: inline-block;
  margin-bottom: 1px;

  background-image: -webkit-linear-gradient(#28A0E5, #015E94);
  background-image: -moz-linear-gradient(#28A0E5, #015E94);
  background-image: -ms-linear-gradient(#28A0E5, #015E94);
  background-image: linear-gradient(#28A0E5, #015E94);

  -webkit-font-smoothing: antialiased;
  border: 0;
  padding: 1px;
  height: 30px;
  text-decoration: none;

  -moz-border-radius: 4px;
  -webkit-border-radius: 4px;
  border-radius: 4px;

  -moz-box-shadow: 0 1px 0 rgba(0,0,0,0.2);
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);
  box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2);

  cursor: pointer;

  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;

    span {
        display: block;
        position: relative;
        padding: 0 12px 0 44px;
        height: 30px;

        background: #1275FF;
        background-image: -webkit-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
        background-image: -moz-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
        background-image: -ms-linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);
        background-image: linear-gradient(#7DC5EE, #008CDD 85%, #30A2E4);

        font-size: 14px;
        line-height: 30px;
        color: white;
        font-weight: bold;
        font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
        text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.2);

        -moz-box-shadow: inset 0 1px 0 rgba(255,255,255,0.25);
        -webkit-box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);
        box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.25);

        -moz-border-radius: 3px;
        -webkit-border-radius: 3px;
        border-radius: 3px;

        &:before {
            content: '';
            display: block;
            position: absolute;
            left: 11px;
            top: 50%;
            width: 23px;
            height: 24px;
            margin-top: -12px;
            background-repeat: no-repeat;
            background-size: 23px 24px;
        }
    }

    &:before {
        background-image: url("data:image/png;base64,");
}
`;
export default function StripeRegistration() {
    const stripeClientId = process.env.REACT_APP_STRIPE_CLIENT_ID
    const stripeLink = `https://connect.stripe.com/oauth/authorize?response_type=code&client_id=${stripeClientId}`
    return (
        <>
            <StripeConnect 
                href={stripeLink} 
            >
                <span>Connect with Stripe</span>
            </StripeConnect>
        </>
    )
}